.layout-scheme-container {
    padding: 20px;
}

.controls {
    margin-bottom: 20px;
    display: flex;
    gap: 10px;
}

.controls button,
.controls .upload-btn {
    padding: 8px 16px;
    background-color: #1890ff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.controls button:hover,
.controls .upload-btn:hover {
    background-color: #40a9ff;
}

.upload-btn {
    position: relative;
    overflow: hidden;
}

.upload-btn input {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    width: 100%;
    height: 100%;
    cursor: pointer;
}

.layout-content {
    display: flex;
    gap: 20px;
}

.objects-panel {
    width: 200px;
    background-color: #f4f4f4;
    padding: 10px;
    border-radius: 5px;
}

.object-item {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 10px;
    background-color: white;
    margin-bottom: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    cursor: grab;
}

.object-item:hover {
    background-color: #f0f0f0;
}

.object-image {
    width: 40px;
    height: 40px;
}

.board {
    flex: 1;
    height: 500px;
    background-color: #e8ecef;
    border: 2px dashed #aaa;
    position: relative;
}

.placed-object {
    position: absolute;
    cursor: move;
}

.placed-object .object-image {
    width: 50px;
    height: 50px;
}
